@import url(https://fonts.googleapis.com/css2?family=Share+Tech+Mono&display=swap);
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  background-image: url(/Drum-Machine/static/media/Gradient_bg_1.4f7f68de.jpg);
  background-size: cover;
  overflow: hidden;

  width: 100vw;
  height: 100vh;
  min-height: 900px;

  display: flex;
  justify-content: center;
  align-items: center;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  height: 600px;
  width: 450px;
  min-width: 450px;
  min-height: 500px;
  background-color: rgba(250, 250, 250, 0.2);
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.1);
  -webkit-backdrop-filter: blur(2px);
          backdrop-filter: blur(2px);
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
  border-radius: 16px;
}

.App #title {
  position: absolute;
  top: 1%;
  font-family: "Share Tech Mono", monospace;
}

.App #drum-machine {
  background-color: transparent;
  height: 90%;
  width: 95%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  padding: 15px;
  border: none;
  border-radius: 16px;
}

.App #drum-machine #display-container {
  width: 90%;
}

.App #drum-machine #display-container #display {
  height: 100px;
  width: 100%;
  border-radius: 16px;
  background-color: rgba(0, 0, 0, 0.6);
  color: rgba(250, 250, 250, 0.9);
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 10px;
  font-family: "Share Tech Mono", monospace;
}

.App #drum-machine #pad-container {
  height: 75%;
  width: 90%;
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: (1fr)[3];
      grid-template-columns: repeat(3, 1fr);
  -webkit-column-gap: 20px;
          grid-column-gap: 20px;
          column-gap: 20px;
  grid-row-gap: 20px;
  row-gap: 20px;
}

.App #drum-machine #pad-container .drum-pad {
  cursor: pointer;
  background-color: rgba(250, 250, 250, 0.3);
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.1);
  border: none;
  border-radius: 16px;
  font-size: 30px;
}

.App #drum-machine #pad-container .drum-pad_active {
  cursor: pointer;
  background-color: red;
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.1);
  border: none;
  border-radius: 16px;
  font-size: 30px;
}

@media (min-width: 200px) and (max-width: 480px) {
  .App {
    width: 300px;
    min-width: 300px;
  }
  .App #title {
    font-size: 20px;
    top: 15px;
  }
  .App #drum-machine {
    height: 80%;
  }
  .App #drum-machine #pad-container {
    height: 60%;
  }
}
